.vpc-table {
  padding: 0.26rem 0.3rem;
}
.vpc-table .ant-table-tbody > tr > td,
.vpc-table .ant-table-thead > tr > th {
  padding-left: 0.6rem;
}
.vpc-table .top {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 0.2rem;
}
.vpc-table .top .tip {
  color: #9696A3;
  padding: 0.08rem 0;
  line-height: 1;
}
.vpc-table .create {
  line-height: 0.4rem;
  padding: 0 0.16rem;
  color: #fff;
  background: #5C5EE4;
  border-radius: 0.04rem;
  cursor: pointer;
}
.vpc-table .area {
  margin-bottom: 0.25rem;
  line-height: 0.18rem;
  font-size: 0.16rem;
  color: #040E34;
}
.vpc-table .area img {
  height: 0.18rem;
  margin-right: 0.1rem;
}
.vpc-table .area img,
.vpc-table .area span {
  display: inline-block;
  vertical-align: middle;
}
.vpc-table table {
  border: 1px solid #D9DBE1;
}
.vpc-table .vpc-box {
  margin-bottom: 0.3rem;
}
/* create-vpc */
.create-vpc {
  /* total-box */
}
.create-vpc .com-tit .tit {
  color: #040E34;
  line-height: 1;
  margin-bottom: 0.32rem;
  font-size: 0.18rem;
}
.create-vpc .com-card {
  padding: 0.56rem 0.3rem 1.45rem 0.3rem;
}
.create-vpc .ant-row {
  margin-bottom: 0.3rem;
}
.create-vpc .ant-col {
  margin-bottom: 0.3rem;
}
.create-vpc .ant-col .item {
  border: 1px solid #D9DBE1;
  border-radius: 0.04rem;
  padding: 0.13rem 0.16rem;
  line-height: 0.28rem;
  position: relative;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.create-vpc .ant-col .item:hover {
  border-color: #5C5EE4;
}
.create-vpc .ant-col .item .num {
  width: 0.28rem;
  height: 0.28rem;
  line-height: 0.28rem;
  border: 1px solid #D9DBE1;
  display: inline-block;
  text-align: center;
  border-radius: 50%;
  font-size: 0.12rem;
  color: #525666;
  margin-right: 0.1rem;
}
.create-vpc .ant-col .item .num.cur {
  border-color: #5C5EE4;
  color: #5C5EE4;
}
.create-vpc .ant-col .item .num:hover {
  border-color: #5C5EE4;
}
.create-vpc .country {
  width: 0.3rem;
  height: 0.195rem;
  margin-right: 0.12rem;
  border: 0;
  vertical-align: top;
  font-size: 0;
}
.create-vpc .l-area {
  display: flex;
  align-items: center;
}
.create-vpc .data-center .r-area {
  font-size: 0;
  line-height: 0;
}
.create-vpc .c-active {
  background: #fff !important;
  box-shadow: 0 0 0.2rem 0.05rem rgba(137, 138, 228, 0.3);
}
.create-vpc .c-active.c-hover {
  border-color: transparent !important;
}
.create-vpc .c-active::after {
  content: '';
  width: 0.2rem;
  height: 0.2rem;
  position: absolute;
  text-align: center;
  line-height: 0.2rem;
  top: 0;
  right: 0;
  z-index: 2;
  background: url(../../../../assets/images/cloud/check.png) no-repeat;
  background-size: 100%;
}
.create-vpc .vpc .s-tit {
  line-height: 1;
  margin-bottom: 0.12rem;
  font-size: 0.16rem;
  color: #040E34;
}
.create-vpc .vpc .ant-col {
  margin-bottom: 0;
}
.create-vpc .vpc .ant-col label {
  font-size: 0.16rem;
  color: #040E34;
  line-height: 1;
}
.create-vpc .vpc .ant-col .ant-input {
  width: 3.9rem;
  line-height: 0.34rem;
  border-radius: 0.04rem;
}
.create-vpc .vpc .custom {
  display: flex;
  align-items: center;
}
.create-vpc .vpc .custom .tip {
  font-size: 0.13rem;
  color: #9696A3;
  margin-right: 0.1rem;
}
.create-vpc .vpc .custom .ant-select-selector {
  line-height: 0.22rem;
  width: 0.56rem;
  padding: 0 0.04rem 0 0.08rem;
}
.create-vpc .vpc .custom .dot {
  width: 0.08rem;
  text-align: center;
  color: #9696A3;
}
.create-vpc .vpc .custom .ant-input,
.create-vpc .vpc .custom .ant-input-number {
  width: 0.44rem;
  text-align: center;
  padding-left: 0.05rem;
  padding-right: 0.05rem;
}
.create-vpc .vpc .custom .ant-input .ant-input-number-input,
.create-vpc .vpc .custom .ant-input-number .ant-input-number-input {
  padding: 0;
  text-align: center;
}
.create-vpc .total-box {
  background: #fff;
  z-index: 20;
  width: 100%;
  height: 1.45rem;
  position: fixed;
  bottom: 0;
  left: 0;
  padding-left: 265px;
  box-shadow: 0 0 0.2rem 0.05rem rgba(137, 138, 228, 0.3);
}
.create-vpc .total-box .content {
  width: 77.945619%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 0.2rem 0;
}
.create-vpc .total-box .item {
  display: flex;
  font-size: 0.13rem;
  color: #9696A3;
}
.create-vpc .total-box p {
  margin-bottom: 0;
}
.create-vpc .total-box .config .ant-select {
  min-width: 1rem;
}
.create-vpc .total-box .config .duration {
  position: relative;
}
.create-vpc .total-box .l-config:first-child {
  margin-right: 1.3rem;
}
.create-vpc .total-box .tit {
  font-size: 0.16rem;
  color: #040E34;
  font-weight: 400;
  margin-bottom: 0.2rem;
  line-height: 1;
}
.create-vpc .total-box .des {
  line-height: 0.28rem;
  display: flex;
  align-items: center;
}
.create-vpc .total-box .des img {
  border-radius: 0.04rem;
}
.create-vpc .create-btn {
  width: 1.6rem;
  line-height: 0.48rem;
  height: 0.48rem;
  border-radius: 0.04rem;
  background: #5C5DE3;
  color: #fff;
  text-align: center;
  font-size: 0.16rem;
  margin-top: 0.2rem;
  cursor: pointer;
}
@media screen and (max-width: 1600px) {
  .create-vpc .vpc .custom .ant-select-selector {
    width: 0.65rem;
  }
  .create-vpc .vpc .custom .ant-input-number,
  .create-vpc .vpc .custom .ant-input {
    width: 0.5rem;
  }
}
@media screen and (max-width: 1200px) {
  .create-vpc .vpc .custom .ant-select-selector {
    width: 0.8rem;
  }
  .create-vpc .vpc .custom .ant-input-number,
  .create-vpc .vpc .custom .ant-input {
    width: 0.6rem;
  }
}
@media screen and (max-width: 992px) {
  .create-vpc .vpc .custom .ant-select-selector {
    width: 1rem;
  }
  .create-vpc .vpc .custom .ant-input-number,
  .create-vpc .vpc .custom .ant-input {
    width: 0.8rem;
  }
}
